.skills{
    max-width: 1216px;
    padding: 0 16px;
    margin: 0 auto 8rem;
    &__title{
        font-size: 2.25rem;
        margin: 0;
        font-style: normal;
        font-weight: 600;
        padding: 1rem 0;
        border-bottom: 2px solid black;
        @media(max-width:$mobile){
            text-align: center;
    }
}
}

.skills-list{
    display: grid;
    padding: 8rem 0 0;
    grid-template-columns: 1fr 1fr ;
    column-gap: 0.5rem;
        @media(max-width:$mobile){
            grid-template-columns: 1fr;
            padding: 5rem 0 0;
        }
    &__item{
        color: $gray;
        padding: 12px 0 0px;
        min-height: 40px;
        @media(max-width:$mobile){
            padding-left: 1.5rem;
        }
        ::before{
            content: '' ;
            position: absolute;
            top:10px;
            left: 0;
            display: block;
            width: 18px;
            height: 18px;
            background: url('src/assets/img/check-black.svg');
        }
    } 
    &__list{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(280px,1fr));
        list-style-type:none;
        padding:0;
        margin-top: calc(1.5rem + 4px);
        column-gap: 1.5rem;

    }
    &__title{
        font-weight: 600;
        font-size: 2rem;
        line-height: 2.75rem;
        margin: 0;
        @media(max-width:$mobile){
        padding-left: 1.5rem;}

        &::before{
            @media(max-width:$mobile){
            padding-left: 1.5rem;}

            content: '';
            border-radius: 88px;
            height: 4px;
            display: block;
            background: $green;
            border-radius: 8px;
            margin-bottom: 1.5rem;
            .skills-list--violet & {
                background:$violet;
            }
            .skills-list--blue & {
                background: $blueish;
            }
        }
    }
}